import {add, cells, Pos, Tetra} from "./Parquet";


const offsets = [
    [],
    [{row: 0, col: 0}, {row: -1, col: 0}, {row: -1, col: 1}, {row: -2, col: 1},],
    [{row: 0, col: 0}, {row: 0, col: 1}, {row: -1, col: 1}, {row: -1, col: 2},],
    [{row: 0, col: 0}, {row: 0, col: 1}, {row: 1, col: 1}, {row: 1, col: 2},],
    [{row: 0, col: 0}, {row: 1, col: 0}, {row: 1, col: 1}, {row: 2, col: 1},],
]

const kinds = [1, 2, 3, 4]

const positions = (t: Tetra): Pos[] => offsets[t.k].map(v => add(v, t.p))

type TetraId = number // 1 .. no. of max valid placement


// util:
const groupBy = <T, K extends keyof any, V>(list: T[], getKey: (item: T) => K, getValue: (item: T) => V) =>
    list.reduce((previous, currentItem) => {
        const group = getKey(currentItem);
        if (!previous.has(group))
            previous.set(group, []);
        previous.get(group)!.push(getValue(currentItem));
        return previous;
    }, new Map<K, V[]>());


export interface ProblemStatement {
    placements: Tetra[],
    clauses: TetraId[][],
}


export const buildProblem = (n: number, excluded: Pos[]): ProblemStatement => {
    const posCode = (p: Pos) => p.col + n * p.row
    const exludedSet = new Set(excluded.map(posCode))
    const inRange = (p: Pos) => 0 <= p.col && p.col < n && 0 <= p.row && p.row < n

    const allTetras: Tetra[] = Array.from(cells(n)).flatMap(p => kinds.map(k => ({p, k})))
    const allValidTetras = allTetras.filter(t => {
        const ps = positions(t)
        return ps.every(p => inRange(p))
    }).filter(t => {
        const ps = positions(t)
        return ps.every(p => !exludedSet.has(posCode(p)))
    })

    const tetraCode = (t: Tetra) => posCode(t.p) * kinds.length + t.k

    const tetra2id = new Map(allValidTetras.map((t, index) => [tetraCode(t), index]))
    const tetraId = (tCode: number): TetraId => tetra2id.get(tCode)! + 1

    const tetra2pos = allValidTetras.flatMap(t => positions(t).map(p => [posCode(p), tetraCode(t)]))
    const pos2tetra = groupBy(tetra2pos, x => x[0], x => x[1])

    function atLeastOne(vs: number[]) { return vs.map(tetraId) }

    function* atMostOne(vs: number[]) {
        for (let i = 0; i < vs.length; i++)
            for (let j = i + 1; j < vs.length; j++)
                yield [-tetraId(vs[i]), -tetraId(vs[j])]
    }

    const rv: TetraId[][] = []
    for (let [posCode, tetraCodes] of pos2tetra) {
        rv.push(atLeastOne(tetraCodes))
        for (const clause of atMostOne(tetraCodes))
            rv.push(clause)
    }

    return {
        placements: allValidTetras,
        clauses: rv,
    }
}


export const toDimacs = (problem: ProblemStatement): string => {
    const header = `p cnf ${problem.placements.length + 1} ${problem.clauses.length}\n`
    const clauses = problem.clauses.map(xs => xs.join(" ") + " 0").join("\n")
    return header + clauses
}


export const interpretResults = (problem: ProblemStatement, output: string): Tetra[] => {
    const vs = output.trim().split(" ").map(x => parseInt(x) - 1).filter(x => x >= 0)
    console.log(vs)
    const s = new Set(vs)
    const result = problem.placements.filter((x, ix) => s.has(ix))
    return result
}