import {HTMLAttributes, SVGAttributes} from "react";

export interface Pos {
    col: number,
    row: number,
}

export const add = (a: Pos, b: Pos): Pos => ({col: a.col + b.col, row: a.row + b.row})

export interface Tetra {
    p: Pos,
    k: number,
}

const SIZE = 30

export function* cells(size: number) {
    for (let col = 0; col < size; col++)
        for (let row = 0; row < size; row++)
            yield {col, row}
}

type ParquetProps = {
    n: number,
    excluded: Pos[],
    shapes: Tetra[],
    toggle: (p: Pos) => void,
}

const Parquet = ({n, excluded, shapes, toggle}: ParquetProps) => {
    const id = (p: Pos) => p.col + p.row * n
    const s = new Set(excluded.map(id))
    console.log(s)
    return (
        <svg width={n * SIZE + 10} height={n * SIZE + 10}>
            <g transform={'translate(5,5)'}>
                {Array.from(cells(n)).map(p => <Square p={p} block={s.has(id(p))} onClick={() => toggle(p)}/>)}
                {shapes.map(s => <Shape p={s.p} k={s.k}/>)}
            </g>
        </svg>
    )
}

export default Parquet

type SquareProps = {
    p: Pos,
    block: boolean
} & SVGAttributes<SVGElement>

const Square = ({p, block, ...rest}: SquareProps) => {
    return (
        <rect x={p.col * SIZE} y={p.row * SIZE} width={SIZE} height={SIZE}
              className={block ? "cell-block" : "cell-empty"} {...rest}/>
    )
}


// k -- kind -- 1..4

type ShapeProps = {
    p: Pos,
    k: number,
}

const k1 = (s: number) => `M 0 0 v ${-s} h ${s} v ${-s} h ${s} v ${2 * s} h ${-s} v ${s} h ${-s} v ${-s}`
const k2 = (s: number) => `M 0 0 h ${s} v ${-s} h ${2 * s} v ${s} h ${-s} v ${s} h ${-2 * s} v ${-s}`
const k3 = (s: number) => `M 0 0 h ${2 * s} v ${s} h ${s} v ${s} h ${-2 * s} v ${-s} h ${-s} v ${-s}`
const k4 = (s: number) => `M 0 0 h ${s} v ${s} h ${s} v ${2 * s} h ${-s} v ${-s} h ${-s} v ${-2 * s}`

const DATA = [k1, k2, k3, k4]

const Shape = ({p, k}: ShapeProps) => {
    const data = DATA[k - 1]
    const tr = `translate(${p.col * SIZE},${p.row * SIZE})`
    return (<>
        <path d={data(SIZE)} transform={tr} className="shape"/>
        <circle r={3} cx={p.col * SIZE + SIZE / 2} cy={p.row * SIZE + SIZE / 2} className="mark"/>
    </>)
}

type ShapeDisplayProps = {
    p: Pos,
    k: number
}
const ShapeDisplay = ({p, k, ...rest}: ShapeDisplayProps & HTMLAttributes<SVGElement>) => {
    const pad = 5
    const tr = `translate(${5},${5})`
    return (
        <svg width={3 * SIZE + pad * 2} height={3 * SIZE + pad * 2} {...rest}>
            <g transform={tr}>
                <Shape p={p} k={k}/>
            </g>
        </svg>
    )
}

export const ShapeList = () => {
    return (
        <div className="d-flex flex-row">
            <ShapeDisplay p={{col: 1, row: 2}} k={1} className="m-2"/>
            <ShapeDisplay p={{col: 0, row: 2}} k={2} className="m-2"/>
            <ShapeDisplay p={{col: 0, row: 1}} k={3} className="m-2"/>
            <ShapeDisplay p={{col: 0, row: 0}} k={4} className="m-2"/>
        </div>
    )
}