import React, {useEffect, useState} from 'react';
import './App.css';
import Parquet, {Pos, ShapeList, Tetra} from "./Parquet";
import {buildProblem, interpretResults, ProblemStatement, toDimacs} from "./model";


// worker protocol
const queries = {VERIFY: "VERIFY"}
const responses = {
    PROGRESS: "PROGRESS",
    READY: "READY",
    STDOUT: "STDOUT",
    STDERR: "STDERR",
    VERIFICATION_COMPLETE: "VERIFICATION_COMPLETE"
}

function App() {
    const [solverStatus, setSolverStatus] = useState("")
    const [worker, setWorker] = useState<Worker>()

    const [problem, setProblem] = useState<ProblemStatement>()

    function postZ3Message(query: string, payload: any) {
        console.info("[Window] ‚Üí Z3 (" + query + "):", payload);
        worker!.postMessage({kind: query, payload: payload});
    }

    function onZ3Message(event: { data: { kind: string; payload: any; }; }) {
        console.info("Z3 ‚Üí [Window]:", event);
        var kind = event.data.kind;
        var payload = event.data.payload;
        switch (kind) {
            case responses.PROGRESS:
                // disableButton(payload);
                console.log(payload, "progress");
                setSolverStatus("Z3 is loading...")
                break;
            case responses.READY:
                // enableButton();
                console.log("ready");
                setSolverStatus("Z3 is ready")
                break;
            case responses.STDOUT:
                console.log(payload, "stdout-msg")
                if (payload === "sat" || payload === "unsat") {
                    // read status
                    setSolved(true)
                    setSuccess(payload === "sat")
                } else {
                    // read assignments
                    const result = interpretResults(problem!, payload)
                    console.log(result)
                    setShapes(result)
                }
                // combine results: 2 lines
                // SAT/UNSAT + vars
                break;
            case responses.STDERR:
                console.log(payload, "stderr-msg")
                break;
            case responses.VERIFICATION_COMPLETE:
                // enableButton();
                //console.log("-- Verification complete (" + elapsed + "ms)", "info-msg");
                break;
        }
    }

    useEffect(() => {
        // start Z3 worker
        const w = new window.Worker("worker.js");
        setWorker(w)
    }, [])

    useEffect(() => {
        if (!worker) return
        worker.onmessage = onZ3Message
    }, [worker, onZ3Message])


    const [solved, setSolved] = useState(false)
    const [size, setSize] = useState(4)
    const [excluded, setExcluded] = useState<Pos[]>([
        {row: 0, col: 0},
        {row: 0, col: 3},
        {row: 1, col: 0},
        {row: 3, col: 3},
    ])
    const [success, setSuccess] = useState(false)
    const [shapes, setShapes] = useState<Tetra[]>([])

    const onToggle = (p: Pos) => {
        const ix = excluded.findIndex(v => v.col === p.col && v.row === p.row)
        if (ix === -1) {
            excluded.splice(-1, 0, p)
        } else {
            excluded.splice(ix, 1)
        }
        setExcluded(Array.from(excluded))
    }

    const onClear = () => {
        onReset()
        setExcluded([])
    }

    const onSolve = () => {
        const problem = buildProblem(size, excluded)
        console.log(problem)
        const input = toDimacs(problem)
        setProblem(problem)
        postZ3Message(queries.VERIFY, {args: [], input: input})
    }

    const onReset = () => {
        setSolved(false)
        setShapes([])
    }

    return (
        <div id="app" className="d-flex flex-column justify-content-start align-items-center m-3">
            <div>
                <ol>
                    <li>Wait for Z3 to load...</li>
                    <li>Select a grid size</li>
                    <li>Click on a cell to include/exclude it</li>
                    <li>Solve to see possible arrangement</li>
                </ol>
            </div>
            <div className="d-flex flex-row">
                <span className="m-2">Size:</span>
                <input className="m-2" type={'range'} min={3} max={20} value={size}
                       onChange={x => setSize(parseInt(x.target.value))}/>
                <strong className="m-2">{size}</strong>
            </div>
            <div className="d-flex flex-row">
                {!solved && <button className="btn btn-outline-primary m-2" onClick={onSolve}>Solve</button>}
                {solved && <button className="btn btn-outline-danger m-2" onClick={onReset}>Reset</button>}
                <button className="btn btn-outline-secondary m-2" onClick={onClear}>Clear</button>
            </div>
            <p className="m-1"><em>{solverStatus}</em></p>
            <p className="m-1">
                {!solved && <span>click solve</span>}
                {solved && <strong>{success ? 'SAT ‚úÖ' : 'UNSAT ü§∑‚Äç‚ôÇÔ∏è'}</strong>}
            </p>
            <Parquet n={size} excluded={excluded} shapes={shapes} toggle={onToggle}/>

            <p>Available Shapes:</p>
            <ShapeList />
        </div>
    );
}

export default App
